export default {
	onLoad(){
		uniCloud.callFunction({
		    name: 'ip2Locations',
			data:{'ip':'127.0.0.1'}
		  })
		  .then(res => {
				console.log(res.result)
				this.localIp = res.result
		  });
	},
	data() {
		return {
			localIp:'',
			ip:'',
			locations:'',
		}
	},
	methods: {
		copyText(){
			uni.setClipboardData({
				data: this.localIp
			});
		},
		setIp(e){
			this.ip = e.detail.value
		},
		searchIPLocations(){
			if(this.ip==''){
				uni.showToast({
					title:'请输入ip!'
				})
				return 
			}
			uniCloud.callFunction({
			    name: 'ip2Locations',
				data:{'ip':this.ip}
			  })
			  .then(res => {
				  console.log('jieguo ')
					console.log(res.result)
					this.locations = res.result.content
			  });
		}
	}
}
